import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { convertDataToISO } from 'ionic-angular/util/datetime-util';


/*
  Generated class for the GeneratePdfProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GeneratePdfFormProvider {
  woserviceaddress: any;
  assignment: any;
 
  dataArea;

   externalDataRetrievedFromServer = [
    { name: 'Bartek', age: 34 },
    { name: 'John', age: 27 },
    { name: 'Elizabeth', age: 30 },
];



  constructor(public http: HttpClient) {
    console.log('Hello GeneratePdfProvider Provider');
    

  }

  buildTableBody(data, columns) {
    var body = [];

    body.push(columns);

    data.forEach(function(row) {
        var dataRow = [];

        columns.forEach(function(column) {
            dataRow.push(row[column].toString());
        })

        body.push(dataRow);
    });

    return body;
}

table(data, columns) {
  return {
      table: {
          headerRows: 1,
          body: this.buildTableBody(data, columns)
      }
  };
}

  convertPDF(items){

    this.dataArea = items; 

     if(this.dataArea == 0)
    {
      this.dataArea = " ";
      console.log("Here11111");
    }else if(this.dataArea == undefined)
    {
      this.dataArea = "";
      console.log("Here 1");
    }else if(this.dataArea == null)
    {
      this.dataArea = "";
      console.log("Here 2");
    }
    
   return this.dataArea;

  }
 
 

  generatePdfForm(items,soTypes, formType) {
    console.log(" --> called work order service adapter calling ()(()) ");
    console.log("item.."+items);
    this.woserviceaddress = items.json.woserviceaddress[0];
    this.assignment = items.json.assignment[0];


   console.log(items.json.siteid);
 

    return new Promise(resolve => {
      debugger;
      console.log(
        "came inside to complaint pdf form generation --- >>>."
      );


      var dd = {
        content: [
          //Header start
          {
            alignment: 'justify',
            style: 'tableExample',
            table: {
              widths: [100, 200, 200],

              body: [
                [{ text: 'Area Code : '+this.convertPDF(items.json.siteid), aligment: 'left' }, { rowSpan: 2, text: 'SERVICE ORDER', bold: true, fontSize: 18, aligment: 'center' }, {
                  rowSpan: 2, image: 'building',
                  width: 200, aligment: 'right'
                }],
                [{ text: 'Area :' +this.convertPDF(items.json.sitearea), aligment: 'left' }, '', '']


              ]
            },
           // layout: 'noBorders'
          },
          //Header end

          //Body start
          {
            alignment: 'left',
            style: 'tableExample',
            table: {
              widths: [100, 150, 100, 150],

              body: [
                [{ text: 'Printed By:', bold: true }, { text: this.convertPDF(items.json.reportedby)}, { text: 'Printed Date & Time:', bold: true }, { text: this.convertPDF(items.json.reportdate) },],
                [{ text: 'Service Order No:', bold: true }, { text: this.convertPDF(items.json.wonum) +'-'+this.convertPDF(items.json.ta0bcrmnum) }, { text: 'Last Date Bill:', bold: true }, { text: this.convertPDF(items.json.ta0lastbilldate) },],
                [{ text: 'Type of Service Order:', bold: true }, { text: this.convertPDF(items.json.worktype) }, { text: 'Service Description:', bold: true }, { text: this.convertPDF(items.json.description) },],
                [{ text: 'Type of Installation Service:', bold: true }, { text: this.convertPDF(items.json.ta0installationservice) }, { text: 'Condition of Installation Service:', bold: true }, { text: this.convertPDF(items.json.ta0serviceflag==true?'Ready':'Not Ready') },],
                [{ text: 'Registered Customer Name:', bold: true, colSpan: 2 }, {}, { text: this.convertPDF(items.json.ta0bpname), colSpan: 2 }, {},],
                [{ text: 'Occupant Name:', bold: true, colSpan: 2 }, {}, { text: this.convertPDF(items.json.ta0bptenantname), colSpan: 2 }, {},],
                [{ text: 'Account No:' }, { text: this.convertPDF(items.json.ta0accountnum), bold: true }, { text: 'Rate:', bold: true }, { text: this.convertPDF(items.json.ta0ratecategory) },],
                [{text: 'Address:',bold: true},{text: this.convertPDF(this.woserviceaddress.formattedaddress),colSpan: 3} ],
                [{ text: 'No of Suppliers', bold: true, alignment: 'left' }, { text: this.convertPDF(items.json.ta0suppliers)},{ text: 'No of Elements', bold: true }, { text: this.convertPDF(items.json.ta0elementnum) }],
                [{ text: 'Voltage:', bold: true }, { text: this.convertPDF(items.json.ta0installationvoltage)},{ text: 'Signal Strengh:', bold: true }, { text: this.convertPDF(items.json.ta0signalstrength) }],        
                 [{ text: 'Phone No:', bold: true }, { text: this.convertPDF(items.json.phone) }, { text: 'MRU:', bold: true }, { text: this.convertPDF(items.json.ta0mru) },],
                [{ text: 'Meter Location:', bold: true }, { text: 'AAAA' }, { text: 'GPS Coordinate:', bold: true }, { text: this.woserviceaddress.latitudey+'-'+this.woserviceaddress.longitudex },],
                [{ text: 'Key:', bold: true }, { text: this.convertPDF(items.json.ta0key) }, { text: 'Type of Premise:', bold: true }, { text: this.convertPDF(items.json.ta0premisetype) },],
                [{ text: 'AMS:', bold: true }, { text: this.convertPDF(items.json.ta0ams) }, { text: 'AMCG:', bold: true }, { text: this.convertPDF(items.json.ta0amcg) },],
                [{ text: 'Work Plan Date:', colSpan: 4, bold: true }, {}, {}, {},],
                [{ text: 'Date & Time (Start):', bold: true }, { text: this.convertPDF(items.json.schedstart) }, { text: 'Date & Time (End):', bold: true }, { text: this.convertPDF(items.json.schedfinish) },],
                [{ text: 'Actual Start Date:', colSpan: 4, bold: true }, {}, {}, {},],
                [{ text: 'Date & Time(Start) :', bold: true }, { text: this.convertPDF(items.json.actstart) }, { text: 'Date & Time (End):', bold: true }, { text: this.convertPDF(items.json.actfinish) },],
                [{ text: 'Remarks:', bold: true }, { text: this.convertPDF(items.json.ta0remarks), colSpan: 3 }, {}, {}],
                [{ text: 'Approved By:', bold: true }, { text: this.convertPDF(items.json.supervisor) }, { text: 'Work Done By:', bold: true }, { text: this.convertPDF(this.assignment.laborcode) },],
				],

            },
            //layout: 'noBorders'
          },
          //Body end

          {text: 'test array', pageBreak: 'before', style: 'subheader'},
          this.table(this.externalDataRetrievedFromServer, ['name', 'age'])




        ],

        images: {
          building: 'data:image/jpeg;base64,..'
        },
        styles: {
          header: {
            fontSize: 18,
            bold: true,
            margin: [0, 0, 0, 10]
          },
          subheader: {
            fontSize: 16,
            bold: true,
            margin: [0, 10, 0, 5]
          },
          tableExample: {
            margin: [0, 5, 0, 15]
          },
          tableHeader: {
            bold: true,
            fontSize: 13,
            color: 'black'
          }
        },
      }
      resolve(dd);
    });

  }

}



